<template>
  <div :style="styleObject" class="scale-box">
    <slot />
  </div>
</template>

<script>
export default {
  components: {},
  props: {
    // width: {
    //   type: Number,
    //   default: 1920,
    // },
    // height: {
    //   type: Number,
    //   default: 864, //864  1080
    // },
  },
  data() {
    return {
      width:0,
      height:0,
      scale: this.getScale(),
    };
  },
  computed: {
    styleObject() {
      const obj = {
        transform: `scale(${this.scale}) translate(-50%, -50%)`,
        WebkitTransform: `scale(${this.scale}) translate(-50%, -50%)`,
        width: this.width + "px",
        height: this.height + "px",
      };
      return obj;
    },
  },
  mounted() {
    this.width = window.innerWidth;
    this.height = window.innerHeight;
    this.getScale();
    window.addEventListener("resize", this.setScale);
  },
  beforeDestroy() {
    window.addEventListener("resize", this.setScale);
  },
  methods: {
    getScale() {
      // 固定好16:9的宽高比，计算出最合适的缩放比，宽高比可根据需要自行更改
      console.log(window.innerWidth, "window.innerWidth");
      const ww = window.innerWidth / this.width;
      const wh = window.innerHeight / this.height;
      return ww < wh ? ww : wh;
    },
    setScale() {
      // 获取到缩放比，设置它
      const scale = this.getScale();
      this.scale = scale;
    },
  },
};
</script>

<style scoped lang="scss">
.scale-box {
  transform-origin: 0 0;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: 0.3s;
}
</style>



